import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper.scss';
import './visual.css'

const MainVisual = () => {
    return (
        <Swiper
            className='swiper-container'      
            slidesPerView={1}
            onSwiper={(swiper) => console.log(swiper)}
            onSlideChange={() => console.log("slide change")}
        >
            <SwiperSlide>
                <div>
                    <img src="./images/main_slider_1.jpg" alt="" /> 
                    <h2>탐라는 전기차</h2>
                </div>
            </SwiperSlide>
            <SwiperSlide>
                <div>
                    <img src="./images/main_slider_2.jpg" alt="" /> 
                    <h2>전기차와 함께 제주 여행을<br/>준비하는 당신에게</h2>
                </div> 
            </SwiperSlide>
            <SwiperSlide>
                <div>
                    <img src="./images/main_slider_3.jpg" alt="" /> 
                    <h2>전기차에 대한 모든 정보와 꿀팁!</h2>
                </div>
            </SwiperSlide>
        </Swiper>
    );
};

export default MainVisual;