import React, { useState } from 'react';

const Menu = () => {
    const list = ['인기 콘텐츠', '전기차 소개', '슬기로운 전기차 여행', '충전 및 문제 대처법', 'FAQ', '제주 전기차 충전소 찾기', '제주 전기차 뉴스']
    const [menu, setMenu] = useState(list)
    return (
        <ul className="list-menu">
            {
                menu.map((item, index) => <li key={index}>{item}</li>)
            }
        </ul>
    );
};

export default Menu;