import React, { useState } from 'react';
import imglist from '../../assets/api/image'
import { AiOutlinePlus } from "react-icons/ai";


const ConBox1 = () => {
    const [image, setImage] = useState(imglist)
    return (
        <>
            <ul className="card-list">
                {
                    image.map(item => <li kry={item.id}>
                        <img src={item.img} alt=""/>
                    </li>)
                }
            </ul>
            <p className="more">
                <i><AiOutlinePlus/></i>
                <span>더보기</span>
            </p>
        </>
    );
};

export default ConBox1;